.contacts {
  // max-width: 480px;
  height: 507px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 50px;
  padding-bottom: 50px;
  align-items: center;

  background: url(../images/contacts/png/DSC_1xmob.png), rgba(17, 21, 24, 1);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background: url(../images/contacts/png/DSC_2xmob.png), rgba(17, 21, 24, 1);
    background-size: cover;
  }

  @media screen and (min-width: 768px) {
    // max-width: 768px;
    height: 520px;
    background: url(../images/contacts/png/DSC_1xtablet.png), rgba(17, 21, 24, 1);
    background-size: cover;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background: url(../images/contacts/png/DSC_2xtablet.png), rgba(17, 21, 24, 1);
      background-size: cover;
    }
  }

  @media screen and (min-width: 1344px) {
    max-width: 1680px;
    height: 840px;
    padding-bottom: 70px;
    background: url(../images/contacts/png/DSC_1xdesktop.png), rgba(17, 21, 24, 1);
    background-size: cover;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background: url(../images/contacts/png/DSC_2xdesktop.png), rgba(17, 21, 24, 1);
      background-size: cover;
    }
  }

  .container {
    min-width: 320px;
    margin-left: auto;
    margin-right: auto;
  }

  @media screen and (min-width: 1344px) {
    min-width: 1344px;
    min-height: 840px;
    padding-top: 100px;
    padding-bottom: 62px;
  }
}

.contacts img {
  max-width: 100%;
  opacity: 20%;
  height: auto;
}

.contacts__registration {
  min-width: 280px;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
  text-align: center;

  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.35;
  letter-spacing: 0.03em;
  color: var(--white-color);

  @media screen and (min-width: 768px) {
    min-width: 688px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 20px;
    padding-left: 20px;

    font-weight: 800;
    font-size: 26px;
    line-height: 1.3;
  }
  @media screen and (min-width: 1344px) {
    min-width: 1092px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 100px;
    padding-left: 100px;

    text-align: center;
    font-weight: 800;
    font-size: 46px;
    line-height: 1.3;
  }
}

.contacts__request {
  min-width: 280px;
  margin-top: 30px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  padding-right: 35px;
  padding-left: 35px;
  text-align: center;

  font-weight: 600;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: 0.08em;
  color: var(--white-color);

  @media screen and (min-width: 768px) {
    min-width: 452px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px;
    padding-right: 120px;
    padding-left: 120px;

    font-weight: 600;
    font-size: 14px;
    line-height: 1.21;
    letter-spacing: 0.08em;
  }
  @media screen and (min-width: 1344px) {
    min-width: 1090px;
    margin-top: 50px;
    margin-bottom: 56px;
    margin-right: auto;
    margin-left: auto;

    text-align: center;
    font-size: 30px;
    line-height: 1.23;
    letter-spacing: 0.08em;
  }
}

.contacts__accent {
  color: var(--accent-color);
}

.contacts__form-label {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contacts__form-input {
  box-sizing: border-box;
  width: 280px;
  height: 48px;
  padding-top: 15px;
  padding-left: 20px;
  padding-bottom: 15px;
  color: var(--white-color);
  border: 1px solid var(--white-color);
  background: transparent;

  &:not(:last-child) {
    margin-top: 20px;

    @media screen and (min-width: 768px) {
      margin-top: 30px;
    }
  }

  @media screen and (min-width: 768px) {
    width: 390px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 15px;
    padding-left: 28px;
    padding-bottom: 15px;

    font-weight: 600;
    font-size: 14px;
    line-height: 1.21;
  }
  @media screen and (min-width: 1344px) {
    min-width: 478px;
    height: 70px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 23px;
    padding-left: 40px;
    padding-bottom: 23px;

    font-weight: 600;
    font-size: 20px;
    line-height: 1.2;
    letter-spacing: 0.08em;
  }
}

input:valid:not(:placeholder-shown) {
  border-color: var(--white-color);
}

input:invalid:not(:placeholder-shown) {
  border-color: #ff0000;
}

input:invalid:not(:placeholder-shown) + .form-error {
  display: block;
}

::placeholder {
  color: var(--white-color);
  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;
  letter-spacing: 0.08em;
}

.contacts__form-error {
  width: 362px;
  color: #ff0000;
  text-align: center;
  font-size: 10px;
  // display: block;
  margin-top: 3px;
  display: none;
}

.contacts__btn {
  display: flex;
  margin-top: 64px;
  margin-left: auto;
  padding: 14px 10px;
  margin-right: auto;

  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  cursor: pointer;

  background-color: var(--accent-color);
  box-shadow: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

  @media screen and (min-width: 768px) {
    margin-top: 31px;
    padding: 14px 14px;
  }

  @media screen and (min-width: 1344px) {
    min-width: 354px;
    padding: 26px 25px;
    margin-top: 80px;
  }

  transition: color 250ms var(--cubic), background-color 250ms var(--cubic);

  &:hover,
  &:focus {
    background-color: var(--white-color);
  }

  &:hover .contacts__btn-text,
  &:focus .contacts__btn-text {
    color: var(--accent-color);
  }
}

.contacts__btn-text {
  align-items: center;
  text-align: center;
  justify-content: center;

  font-weight: 700;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: var(--white-color);

  @media screen and (min-width: 1344px) {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.21;
    letter-spacing: 0.14em;
  }
}
